const getPattern = (number, width, height) => {
  let timer = 0;
  let x = [];

  if (number === 0) {
    timer = [0];
    x = [Math.random() * (width * 0.8 - width * 0.15) + width * 0.15];
  }

  return { timer, x };
};

const createProductSpawnSystem = () => {
  let delayTime = 2;
  let pattern;
  let timer = 0;
  let index = 0;
  let xIndex = 0;
  return {
    update(delta, worldState, { width, height, productTex, fakeProductTex }) {
      const { difficulty, products } = worldState;

      if (worldState.stateTime > delayTime && pattern === undefined) {
        // const { minNumber, maxNumber } = getShootTypeBaseOnDifficulty(
        //   difficulty
        // );
        // pattern = getPattern(
        //   Math.round(Math.random() * (maxNumber - minNumber) + minNumber),
        //   width,
        //   height
        // );
        pattern = getPattern(0, width, height);
        timer = worldState.stateTime + pattern.timer[index];
      }

      if (
        worldState.stateTime > delayTime &&
        worldState.stateTime > timer &&
        pattern !== undefined
      ) {
        index = Math.min((index += 1), pattern.timer.length);
        timer = worldState.stateTime + pattern.timer[index];

        // loop to check every same timer products
        let loop = 1;
        let tmpIndex = index - 1;
        while (true) {
          if (
            pattern.timer[tmpIndex] === pattern.timer[tmpIndex + 1] &&
            tmpIndex < pattern.timer.length
          ) {
            tmpIndex = Math.min((tmpIndex += 1), pattern.timer.length);
            loop += 1;
          } else {
            break;
          }
        }

        // add to products list
        for (let i = 0; i < loop; i++) {
          if (i > 0) {
            index = Math.min((index += 1), pattern.timer.length);
          }
          timer = worldState.stateTime + pattern.timer[index];

          // TODO: get random product
          let isFake = true;
          let randomProduct = Math.floor(Math.random() * fakeProductTex.length);
          if (Math.random() > 0.3) {
            isFake = false;
            randomProduct = Math.floor(Math.random() * productTex.length);
          }

          products.push({
            type: randomProduct,
            // type: 2,
            isFake,
            stateTime: 0,
            x: pattern.x[xIndex],
            y: -height / 8,
            speedY: 0,
          });

          xIndex = Math.min((xIndex += 1), pattern.x.length - 1);
        }

        if (index === pattern.timer.length) {
          delayTime =
            worldState.stateTime + getDelayBaseOnDifficulty(difficulty);
          index = 0;
          xIndex = 0;
          pattern = undefined;
        }
      }
    },
  };
};

export default createProductSpawnSystem;
